<template>
  <div class="d-flex min-vh-100 position-relative scroll-section"
       :data-section-name="page.section"
       v-for="(page,key) in PAGES"
       :key="key">
    <component :is="page.component"
               :componentLink="key<=PAGES.length-2?PAGES[key+1].link:'#'" :readMore="page.read_more ?? ''"/>
  </div>
</template>
<style>
.btn-orange {
  background-color: #bc4e38 !important;
  border-color: #bc4e38 !important;
  color: #fff !important;
  transition: 1s;
}

.btn-orange:hover {
  background-color: #e06a53 !important;
  border-color: #e06a53 !important;
  transition: 1s;
}
</style>
<script>
import {Pages} from "@/components/Consts/Pages"

export default {
  name: 'App',
  props: ['pages'],
  data() {
    return {
      PAGES: Pages
    }
  },
}
</script>